import React, { Suspense } from "react";
import { isTagEmbed, isTagShop } from "../../../../actions/api";
import MediaIcon from "../../Elements/MediaIcon/MediaIcon";
import Image from "../../Elements/Media/Image";
import Video from "../../Elements/Media/Video";
const Network = React.lazy(() => import("../../Elements/Network/Network"));
const Media = ({ itemData, wallID, ThemeID, ownerId, wall }) => {
  const mediaIcon = itemData.hotspot ? false : true
  const isVideo = (itemData.type === 3 || itemData.type === 5) && (wall.ThemeRule.autoPlay == 1 || !isTagShop) ? true : false
  return <div className="tb_sp_media_wrap">
    {isTagEmbed ? <div className="tb_sp_social_top_">
      <Suspense fallback={<></>}><Network networkClass={'tb_sp_social__ico'} network={itemData.network} isDefault={wall.ThemeRule.iconType === 1 ? true : false} ThemeRule={wall.ThemeRule} /></Suspense>
    </div> : null}
    <MediaIcon itemData={itemData} IconClass={`tb_sp_media_icon${isTagEmbed ? ' tb_sp_media_icon_te' : ''}`} show={mediaIcon} isCenter={isTagEmbed} />
    {isVideo ? <Video VideoClass="tb_sp_video" data={itemData} wallID={wallID} themeID={wall.Personalization.widgetTheme} ownerId={ownerId} size={100} isCover={true} muted={true} />
      : <Image ImageClass="tb_sp_image" data={itemData} wallID={wallID} themeID={ThemeID} ownerId={ownerId} size={100} hotspot={itemData.hotspot ? true : false} />}
  </div>
}
export default Media